.header {
  position: fixed;
  top: 0;
  left: 0vw;
  width: 100vw;
  height: 8.5rem;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1rem;
  background-color: #09090cea;
  box-shadow: 0 8px 16px -12px rgba(0, 0, 0, 1);
  z-index: 996;

  &__buttons {
    display: flex;
    align-items: center;
  }
}

