.logo-link {
  text-decoration: none;
}
.header__logo {
  margin-left: 6rem;
  padding: 0 0.1em;
  font-size: 8em;
  color: white;
  font-family: 'Nunito', sans-serif;

  .logo-decoration {
    padding: 0 0.1em 0.1em 0.1em;
    border-radius: 25%;
    color: white;
    background-color: #089b25;
  }
  &:hover {
    cursor: pointer;
  }
}
@media (min-width: 600px) and (min-height: 965px) {
  .header__logo {
    margin-left: 0;
  }
}
